.toggle-button {
   display: flex;
   flex-direction: column;
   justify-content: space-around;
   height: 24px;
   width: 30px;
   background: transparent;
   border: 0;
   cursor: pointer;
   padding: 0;
   box-sizing: border-box;
}
.toggle-button_line {
    width: 28px;
    height: 4px;
    background-color:  #57b7eb;;
    border-radius: 3px;
}